$tint-1: #021221;
$tint-4: #213147;
$tint-5: #2A5A5B;
$tint-6: #003b77;
$tint-2: #d31a71;
$tint-3: #05bd9b;
$is-true: #7bffa9;
$is-false: #ff9090;
$borde-dian: #5b1973;
$breakpoint-mobile: 480px;
$breakpoint-tablet: 768px;
$breakpoint-desktop: 992px;
$breakpoint-large: 1200px;

@mixin responsive($size) {
    @if $size == mobile {
        @media (max-width: $breakpoint-mobile) {
            @content;
        }
    }
    @else if $size == tablet {
        @media (min-width: calc($breakpoint-mobile + 1px)){
            @content;
        }
    }
    @else if $size == desktop {
        @media (min-width: calc($breakpoint-tablet + 1px)){
            @content;
        }
    }
    @else if $size == large {
        @media (min-width: calc($breakpoint-desktop + 1px)){
            @content;
        }
    }
    @else if $size == extra-large {
        @media (min-width: calc($breakpoint-large + 1px)) {
            @content;
        }
    }
}

.text-large{
    @include responsive(mobile) {
        font-size: 1rem !important;
    }
}

.text-sm-center{
    @include responsive(mobile) {
        text-align: center !important;
        margin: auto;
    }
    @include responsive(extra-large) {
        text-align: inherit !important;
    }
}

h1{
    @include responsive(mobile) {
        font-size: 1.8rem;
    }
}

h2{
    @include responsive(mobile) {
        font-size: 1.6rem;
    }
}

.p-sm{
    @include responsive(mobile) {
        padding: 0 20px;
    }
}

.text-md-large{
    @include responsive(mobile) {
        font-size: .9rem;
    }
    @include responsive(extra-large) {
        font-size: 1.3rem;
    }
}

body{
    font-size: .9rem;
}

.card-top-camp{
    border-top: 6px solid $tint-3;
}

.logo {
    margin-bottom: 15px;
    display: flex;
    justify-content: center;
    align-items: end;

    span {
        font-family: "Madimi One", sans-serif;
        font-weight: 400;
        font-style: normal;
    }

    .u-logo {
        font-size: 2.9rem;
        color: $tint-3;
    }

    .camp-logo {
        font-size: 2.9rem;
        margin-left: 2px;
        color: $tint-1;
    }
}

.btn-ucamp {
    color: #fff;
    background-color: $tint-4;
    border-color: $tint-4;
    box-shadow: none;

    &:hover {
        color: #fff;
        background-color: $tint-3;
        border-color: $tint-3;
        box-shadow: none;
    }
}

.btn-ucamp-border {
    color: $tint-1;
    background-color: none;
    border: 2px solid $tint-1;
    box-shadow: none;

    &:hover {
        color: white;
        background-color: $tint-1;
        border: 2px solid $tint-1;
        box-shadow: none;
    }
}

.btn-2-ucamp-border {
    color: $tint-2;
    background-color: none;
    border: 2px solid $tint-2;
    box-shadow: none;

    &:hover {
        color: white;
        background-color: $tint-2;
        border: 2px solid $tint-2;
        box-shadow: none;
    }
}

.btn-3-ucamp {
    color: #fff !important;
    background-color: $tint-3;
    border: 2px solid $tint-3;
    box-shadow: none;
    text-decoration: none !important;

    &:hover {
        color: #fff;
        background-color: darken($tint-3, 2);
        border: 2px solid darken($tint-3, 2);
        box-shadow: none;
    }
}

.btn-2-ucamp {
    color: #fff;
    background-color: $tint-2;
    border: 2px solid $tint-2;
    box-shadow: none;

    &:hover {
        color: #fff;
        background-color: $tint-3;
        border: 2px solid $tint-3;
        box-shadow: none;
    }
}

