.container-course-progress {
    padding: 10px;
    display:flex;
    justify-content: center;
    align-items: center;

    .progress-text{
        color: $tint-3;
        font-size: 0.9rem;
        font-weight: bold;
    }

    .progress-container {
        width: 100%;
        background-color: #e0e0e0;
        border-radius: 10px;
        overflow: hidden;
        margin: 0;

        .progress-bar {
            height: 15px;
            width: 0%;
            background-color: $tint-3;
            text-align: center;
            line-height: 30px;
            color: white;
            border-radius: 10px;
            font-size: 0.8rem;
        }
    }
}

